import React from "react";
import { Box, Button } from "@mui/material";
import { useParams } from "react-router-dom";
import Deck from "./Deck/Deck";
import ReturnRoute from "../../components/ReturnRoute/ReturnRoute";
import { QueryStats } from "@mui/icons-material";

import "./Decks.css";

const Decks = () => {
  let { id, leader } = useParams();

  const data = require(`../../data/${id}.json`);

  const deckLists = data.decklists.filter(
    (decklist) => decklist.leader.replace("1x", "") === leader
  )[0].lists;

  return (
    <>
      <Box sx={{ my: 2 }}>
        <header>
          <h1>Decks</h1>
          <div className="header-actions">
            {/* <Button variant="outlined" endIcon={<QueryStats />}>
              Stats
            </Button> */}
            <ReturnRoute />
          </div>
        </header>

        {deckLists.map((deckList, index) => (
          <React.Fragment key={index}>
            <Deck list={deckList} />
          </React.Fragment>
        ))}
      </Box>
    </>
  );
};

export default Decks;
