import React from "react";
import {
  Grid2 as Grid,
  AccordionDetails,
  Accordion,
  AccordionSummary,
  Box,
} from "@mui/material";
import { useParams } from "react-router-dom";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import ReturnRoute from "../../components/ReturnRoute/ReturnRoute";

const MetaBreakdown = () => {
  let { id } = useParams();

  const data = require(`../../data/${id}.json`);
  const cards = require("../../data/cards.json");

  //get the top 12 most played leaders (number_of_matches)
  const leaderPresence = data.leaders_presence
    .filter((l) => l.leader !== "Mobile" && l.leader !== "Cx:/")
    .sort((a, b) => b.number_of_matches - a.number_of_matches)
    .slice(0, 12);

  const getLeaderMatchups = () => {
    const leaderMatchups = [];
    leaderPresence.forEach((leaderMatchup) => {
      const currentMatchups = [];
      for (let i = 0; i < leaderMatchup.subject.length; i++) {
        const opponentLeader = leaderMatchup.subject[i];
        //check if the opponent leader is relevant
        const opponentLeaderPresence = leaderPresence.find(
          (leader) => leader.leader === opponentLeader
        );
        if (opponentLeaderPresence) {
          currentMatchups.push({
            opponentLeader: opponentLeader.replace("1x", ""),
            numberOfMatches: leaderMatchup.presence[i],
            numberOfWins: leaderMatchup.subject_wins[i],
          });
        }
      }

      leaderMatchups.push({
        leader: leaderMatchup.leader.replace("1x", ""),
        matchups: currentMatchups.sort((a, b) =>
          a.opponentLeader.localeCompare(b.opponentLeader)
        ),
        overallMatches: leaderMatchup.number_of_matches,
        overallWins: leaderMatchup.wins,
      });
    });
    return leaderMatchups.sort((a, b) => b.overallMatches - a.overallMatches);
  };

  const calculateWinRate = (wins, matches) => {
    return ((wins / matches) * 100).toFixed(2);
  };

  const getCardName = (cardCode) => {
    const card = cards.find((card) => card.cid === cardCode && card.al == null);
    return card ? card.n : cardCode;
  };

  return (
    <Box sx={{ my: 2 }}>
      <header>
        <h1>Meta Breakdown</h1>
        <ReturnRoute />
      </header>
      <div className="meta-breakdown">
        {getLeaderMatchups().map((leaderMatchup) => (
          <Accordion key={leaderMatchup.leader}>
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <strong>
                {getCardName(leaderMatchup.leader)} ({leaderMatchup.leader})
              </strong>{" "}
              - {leaderMatchup.overallMatches} Matches -{" "}
              {leaderMatchup.overallWins} Wins -{" "}
              {calculateWinRate(
                leaderMatchup.overallWins,
                leaderMatchup.overallMatches
              )}
              %
            </AccordionSummary>
            <AccordionDetails>
              <Grid container spacing={2}>
                {leaderMatchup.matchups.map((matchup) => (
                  <Grid size={12} key={matchup.opponentLeader}>
                    <h2>{getCardName(matchup.opponentLeader)}</h2>
                    <h3>{matchup.opponentLeader}</h3>
                    <p>Matches: {matchup.numberOfMatches}</p>
                    <p>Wins: {matchup.numberOfWins}</p>
                    <p>
                      Win Rate:{" "}
                      {calculateWinRate(
                        matchup.numberOfWins,
                        matchup.numberOfMatches
                      )}
                      %
                    </p>
                  </Grid>
                ))}
              </Grid>
            </AccordionDetails>
          </Accordion>
        ))}

        {/* {getLeaderMatchups().map((leaderMatchup) => (
          <div key={leaderMatchup.leader}>
            <h2>{getCardName(leaderMatchup.leader)}</h2>
            <h3>{leaderMatchup.leader}</h3>
            <p>Overall Matches: {leaderMatchup.overallMatches}</p>
            <p>Overall Wins: {leaderMatchup.overallWins}</p>
            <p>
              Overall Win Rate:{" "}
              {calculateWinRate(
                leaderMatchup.overallWins,
                leaderMatchup.overallMatches
              )}
              %
            </p>
            <div className="matchups">
              {leaderMatchup.matchups.map((matchup) => (
                <div key={matchup.opponentLeader}>
                  <h2>{getCardName(matchup.opponentLeader)}</h2>
                  <h3>{matchup.opponentLeader}</h3>
                  <p>Matches: {matchup.numberOfMatches}</p>
                  <p>Wins: {matchup.numberOfWins}</p>
                  <p>
                    Win Rate:{" "}
                    {calculateWinRate(
                      matchup.numberOfWins,
                      matchup.numberOfMatches
                    )}
                    %
                  </p>
                </div>
              ))}
            </div>
          </div>
        ))} */}
      </div>
    </Box>
  );
};

export default MetaBreakdown;
