import React from "react";

import "./Deck.css";
import { Button } from "@mui/material";

const Deck = ({ list }) => {
  const { losses, wins, deck, number_of_matches } = list;

  const deckWinPercentage = () => {
    if (wins === 0) {
      return 0;
    }
    return Math.round((wins / number_of_matches) * 100);
  };

  const getImgURL = (card) => {
    const cardCode = card.replace(/^[0-9]x/, "");
    return `https://en.onepiece-cardgame.com/images/cardlist/card/${cardCode}.png`;
  };

  const copyDeckList = () => {
    navigator.clipboard.writeText(deck.join("\n"));
  };

  return (
    <div className="deck-container">
      <div className="deck-stats">
        <span>{number_of_matches} matches</span>
        <span>{wins} wins</span>
        <span>{losses} losses</span>
        <span>
          <strong>{deckWinPercentage()}% win rate</strong>
        </span>
        <Button variant="contained" onClick={copyDeckList}>
          Copy
        </Button>
      </div>
      <div className="deck">
        {deck.map((card, index) => (
          <div key={index} className="card">
            <img
              key={index}
              src={getImgURL(card)}
              alt={card}
              loading="lazy"
              className="card-img"
            />
            <span>{card}</span>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Deck;
