import React from "react";
import { Button } from "@mui/material";
import { useNavigate } from "react-router-dom";

const ReturnRoute = () => {
  const navigate = useNavigate();

  const handleGoBack = () => {
    navigate(-1);
  };

  return (
    <Button variant="contained" onClick={handleGoBack}>
      Go Back
    </Button>
  );
};

export default ReturnRoute;
