import { Box, List, ListItemButton, ListItemText } from "@mui/material";
import React from "react";
import { Link } from "react-router-dom";

const Overview = () => {
  const data = [
    {
      id: 0,
      name: "OPBounty OP07",
      file: "opbounty-op07",
    },
    {
      id: 1,
      name: "OPBounty OP08",
      file: "opbounty-op08",
    },
    // {
    //   id: 100,
    //   name: "OPBounty (OP07.5) 19.08.2024-25.08.2024",
    //   file: "opbounty-20240819",
    // },
    {
      id: 100,
      name: "OPBounty Last Week",
      file: "opbounty-lw",
    },
  ];

  return (
    <>
      <Box sx={{ my: 2 }}>
        <header>
          <h1>Overview</h1>
        </header>
        <List>
          {data.map((item) => (
            <ListItemButton
              key={item.id}
              component={Link}
              to={`/leader-overview/${item.file}`}
            >
              <ListItemText primary={item.name} />
            </ListItemButton>
          ))}
        </List>
      </Box>
    </>
  );
};

export default Overview;
