import { Container } from "@mui/material";
import React from "react";
import { Route, HashRouter as Router, Routes } from "react-router-dom";
import "./App.css";
import Decks from "./pages/Decks/Decks";
import LeaderOverview from "./pages/LeaderOverview/LeaderOverview";
import Overview from "./pages/Overview/Overview";
import MetaBreakdown from "./pages/MetaBreakdown/MetaBreakdown";

function App() {
  return (
    <Router>
      <Container maxWidth="sm">
        <Routes>
          <Route path="/" element={<Overview />} />
          <Route path="leader-overview/:id" element={<LeaderOverview />} />
          <Route path="leader-overview/:id/decks/:leader" element={<Decks />} />
          <Route
            path="leader-overview/:id/meta-breakdown"
            element={<MetaBreakdown />}
          />
        </Routes>
      </Container>
    </Router>
  );
}

export default App;
