import {
  Box,
  List,
  ListItemButton,
  ListItemIcon,
  ListItemText,
} from "@mui/material";
import React from "react";
import { useParams } from "react-router-dom";
import "./LeaderOverview.css";
import ReturnRoute from "../../components/ReturnRoute/ReturnRoute";
import { QueryStats } from "@mui/icons-material";

const LeaderOverview = () => {
  let { id } = useParams();

  const data = require(`../../data/${id}.json`);
  const cards = require("../../data/cards.json");

  const leadersDeckCount = [
    ...new Set(
      data.decklists
        .filter((decklist) => decklist.lists?.length > 0)
        .map((decklist) => {
          return {
            leader: decklist.leader.replace("1x", ""),
            deckCount: decklist.lists.length,
          };
        })
    ),
  ];

  const leaders = leadersDeckCount.map((leader) => leader.leader);

  const leaderCards = cards.filter(
    (card) => leaders.includes(card.cid) && card.al == null
  );

  const getImgURL = (cardCode) => {
    return `https://en.onepiece-cardgame.com/images/cardlist/card/${cardCode}.png`;
  };

  const getLeaderDeckCount = (leaderCode) => {
    const deckCount = leadersDeckCount.find(
      (leader) => leader.leader === leaderCode
    )?.deckCount;
    let deckText = "Deck";
    if (deckCount > 1) {
      deckText += "s";
    }
    return `${deckCount} ${deckText}`;
  };

  return (
    <>
      <Box sx={{ my: 2 }}>
        <header>
          <h1>Leaders</h1>
          <ReturnRoute />
        </header>
        <List>
          <ListItemButton to={`#/leader-overview/${id}/meta-breakdown`}>
            <ListItemIcon>
              <QueryStats />
            </ListItemIcon>
            <ListItemText primary="Meta Breakdown" />
          </ListItemButton>
          {leaderCards.map((leader) => (
            <ListItemButton
              key={leader.iu}
              to={`#/leader-overview/${id}/decks/${leader.cid}`}
            >
              <img
                loading="lazy"
                src={getImgURL(leader.cid)}
                alt={leader.n}
                className="leader-avatar"
              />
              <div className="info">
                <span className="leader-name">{leader.n}</span>
                <span className="leader-code">{leader.cid}</span>
                <span className="deck-lists">
                  {getLeaderDeckCount(leader.cid)}
                </span>
                <span className="win-rate"></span>
              </div>
            </ListItemButton>
          ))}
        </List>
      </Box>
    </>
  );
};

export default LeaderOverview;
